html {
  box-sizing: border-box;
  font: 100%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  text-align: center;
}

main {
  padding: 10px;
}

.app {
  margin-left: auto;
  margin-right: auto;
  align-items: center;
  justify-content: center;
}

.container {
  width: auto;
  max-width: 80%;
  margin-top: 2rem;
  padding-bottom: 1rem;
  padding-top: 1rem;
  margin-left: auto;
  margin-right: auto;
  max-width: 75rem;
  background-color: var(--primary);
  border-radius: 1rem;
  min-height: 10rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.search {
  width: auto;
  max-width: 90%;
  margin-top: 2rem;
  margin-bottom: 1rem;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0.5rem 0.5rem;
  border-radius: 1rem;
  background: var(--prime-variant);
  display: flex;
}

.search input {
  max-width: 100%;
  width: auto;
  flex: 1;
  border: none;
  padding-right: 1rem;
  background: var(--prime-variant);
  outline: none;
}

.card {
  width: 12rem;
  height: 12rem;
  margin: 1rem;
  background-color: var(--secondary);
  position: relative;
  border-radius: 12px;
  overflow-y: hidden;
  border: none;
  transition: all 1.5s cubic-bezier(0.175, 0.885, 0, 1);
  box-shadow: 0px 13px 10px -7px rgba(0, 0, 0, 0.1);
}

.card div:nth-of-type(1) {
  position: absolute;
  padding: 16px;
  width: 100%;
  top: 0;
}

.card:hover,
.talcard:hover {
  z-index: 5;
  box-shadow: 0px 30px 18px -8px rgba(0, 0, 0, 0.1), 0rem 0rem 2rem rgba(255, 255, 255, 0.99); /* Added white drop shadow */
  background-color: #ddf9f5;
  transform: scale(1.5, 1.5);
}

.card div:nth-of-type(2) {
  width: 100%;
  height: 100%;
}

.card div:nth-of-type(2) img {
  height: 100%;
  width: auto;
  max-height: 100%;
}

.card div:nth-of-type(3) {
  z-index: 2;
  background-color: var(--second-variant);
  padding: 0.2rem 0.1rem 0rem 0.1rem;
  position: absolute;
  bottom: 0;
  right: 0;
  left: 0;
  color: #f0f0f0;
}

.card:hover div:nth-of-type(2) {
  height: 100%;
  opacity: 0.1;
}

.card:hover div:nth-of-type(3) {
  background-color: transparent;
  color: #343739;
}

.card:hover div:nth-of-type(1) {
  opacity: 1;
}

body {
  text-align: center;
  background-color: var(--bg-color);
}

@media screen and (max-width: 1440px) {
  .app {
    padding: 4rem 2rem;
  }
  .search {
    padding: 1rem 1.75rem;
    width: 100%;
  }
  .search input {
    font-size: 1rem;
  }
  .search img {
    width: 20px;
    height: 20px;
  }
}
@media screen and (max-width: 700px) {
  .app {
    padding: 4rem 1rem;
  }
  h1 {
    font-size: 2rem;
  }
  .container {
    margin-top: 2rem;
  }
  .card {
    width: "100%";
    margin: 1rem;
  }
  input {
    max-width: 7.5rem;
  }
}
.ccc {
  display: flex;
  flex-wrap: wrap; /* Allow cards to wrap to new lines */
  padding: 2rem;
  align-items: center;
  justify-content: center;
}

.tcc {
  background-color: var(--prime-variant);
  display: flex;
  flex-wrap: wrap;
  padding: 2rem;
  align-items: center;
  justify-content: center;
  margin: 2rem;
  border-radius: 1rem;
}

.talent-head {
  display: flex;
  align-items: center;
  justify-content: center;
}

.talcard {
  max-width: 80%;
  min-height: 10rem;
  max-height: 10rem;
  overflow-y: scroll;
  position: relative;
  border-radius: 12px;
  border: none;
  transition: all 1.5s cubic-bezier(0.175, 0.885, 0, 1);
  box-shadow: 0px 13px 10px -7px rgba(0, 0, 0, 0.1);
  background-color: var(--secondary);
  width: 100%;
  margin: 1rem;
  margin-left: 1rem;
  margin-right: 1rem;
  display: block;
  justify-content: center;
  padding: 0rem;
}
@media (min-width: 56.25em) {
  .talcard {
    max-width: 40%;
    max-height: 10rem;
    overflow-y: hidden;
  }
}
@media (min-width: 90em) {
  .talcard {
    max-width: 30%;
  }
}
@media (min-width: 56.25em) {
  .talcard {
    display: flex;
    margin-left: auto;
    margin-right: auto;
    padding: 1rem;
  }
}

.talent-img {
  position: relative;
  background-color: var(--second-variant);
  border-radius: 100%;
  width: 5rem;
  height: 5rem;
  align-items: center;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  margin-top: auto;
  margin-bottom: auto;
  max-width: 10rem;
}

.talent-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: auto;
  margin-bottom: auto;
  width: 5rem; /* Minimum width */
  height: 5rem; /* Minimum height */
}

.taltext {
  overflow-y: scroll;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
}

button {
  display: inline-block;
  padding: 0.5rem 1rem;
  border: none;
  border-radius: 0.5rem;
  background-color: var(--prime-variant);
  color: #f0f0f0;
  font-size: 1rem;
  cursor: pointer;
  transition: background-color 0.3s, color 0.3s;
}

button:hover {
  background-color: var(--second-variant);
  color: #343739;
}

.char-icon {
  width: clamp(4.125rem, 4.6875rem + 1vw, 6.25rem);
}

h1,
h2,
h3,
h4 {
  font-weight: 500;
  line-height: 1.1;
  margin-top: 10;
  font-family: "Signika", sans-serif;
}

h1 {
  font-size: clamp(1.25rem, 1rem + 2vw, 2.625rem);
}

h2 {
  font-size: clamp(1.25rem, 0.9375rem + 1.5vw, 2rem);
}

p,
swal2-html-container,
li,
span {
  font-size: 1rem;
  font-weight: 300;
  margin-top: 0;
  font-family: "Signika", sans-serif;
  font-size: auto;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.app {
  color: black;
}

button {
  font-family: "Signika", sans-serif;
}

.search input {
  font-size: 1.11rem;
