}
@media (min-width: 56.25em) {
  .taldeets {
    text-align: left;
  }
}

.talcard h3 {
  font-size: clamp(0.5625rem, 0.625rem + 1vw, 1rem);
}
.talcard p {
  font-size: clamp(0.5rem, 0.5625rem + 1vw, 0.6875rem);
}

:root {
  --bg-color: #ffffff;
  --card-color: darkorchid;
  --text-color: beige;
  --primary: #6200ee;
  --prime-variant: #3700b3;
  --prime-error-text: #ffffff;
  --secondary-text: #000000;
  --secondary: #03dac6;
  --second-variant: #018786;
}

.grid {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto auto;
  gap: 40px;
  width: min(100% - 0.625rem, 1000px);
  margin-left: auto;
  margin-right: auto;
  margin-inline: auto;
}
@media (min-width: 56.25em) {
  .grid {
    grid-template-columns: 2fr 1fr;
  }
}
.grid__main, .grid__sidebar {
  padding: 20px;
}
.grid__main {
  background-color: darkblue;
}
@media (min-width: 56.25em) {
  .grid__sidebar {
    text-align: left;
  }
}
.grid__widget {
  background-color: darkorchid;
  padding: 0.75rem;
}
.grid__widget.green {
  background-color: green;
